export class SizeHandler {
  constructor() {
    this.update();
    window.addEventListener("resize", this.update.bind(this));
  }
  update() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.center_x = this.width / 2;
    this.center_y = this.height / 2;
    this.radius = Math.min(this.center_x, this.center_y);
  }
}
export function keyboardHelper(value) {
  const key = {
    value,
    isDown: false,
    isUp: true,
    press: () => {
    },
    release: () => {
    },
    unsubscribe: () => {
    }
  };
  const downHandler = (event) => {
    if (event.key === key.value) {
      if (key.isUp && key.press)
        key.press(key, event);
      key.isDown = true;
      key.isUp = false;
      event.preventDefault();
    }
  };
  const upHandler = (event) => {
    if (event.key === key.value) {
      if (key.isDown && key.release)
        key.release(key, event);
      key.isDown = false;
      key.isUp = true;
      event.preventDefault();
    }
  };
  window.addEventListener("keydown", downHandler, false);
  window.addEventListener("keyup", upHandler, false);
  key.unsubscribe = () => {
    window.removeEventListener("keydown", downHandler);
    window.removeEventListener("keyup", upHandler);
  };
  return key;
}
export const mod = (a, b) => (a % b + b) % b;
export const cacheUnaryFunction = (f) => {
  const cache = new Map();
  return (x) => {
    if (cache.has(x))
      return cache.get(x);
    const r = f(x);
    cache.set(x, r);
    return r;
  };
};
export const cachedCreateObjectURL = cacheUnaryFunction(URL.createObjectURL);
export const swapContainer = (parent, prev, next) => {
  parent.removeChild(prev);
  parent.addChild(next);
};
